.button {
  position: relative;
  display: inline-block;
  font-size: 0.875rem;
  color: #fff;
  background-color: #408F95;
  border: solid 1px #408F95;
  font-family: "Gotu", sans-serif;
  padding-top: 1rem;
  padding-right: 5.5rem;
  padding-bottom: 1rem;
  padding-left: 2.5rem;
  z-index: 1;
}

.button::before {
  content: "";
  position: absolute;
  display: inline-block;
  border: solid 1px #408F95;
  width: 100%;
  height: 100%;
  top: 0.25rem;
  left: 0.25rem;
  z-index: -2;
}

.button::after {
  display: inline-block;
  background-color: #fff;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -1;
}

.button span::after {
  content: "";
  display: inline-block;
  position: absolute;
  top: 47%;
  transform: translateY(-50%);
  right: 2.5rem;
  width: 2.5rem;
  height: 0.375rem;
  background-image: url("../images/common/more.svg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  opacity: 1;
}

.button span::before {
  content: "";
  display: inline-block;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 2.5rem;
  width: 2.5rem;
  height: 0.375rem;
  background-image: url("../images/common/more-accent.svg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  opacity: 0;
  z-index: 1;
}

@media (hover: hover) {
  .button:hover {
    color: #408F95;
  }
  .button:hover.button span::before {
    opacity: 1;
  }
  .button:hover.button span::after {
    opacity: 0;
  }
  .button:hover::after {
    transform: scale(1, 1);
  }
}
.button.button--contact-form span::after {
  right: 3.5rem;
}

.button.button--contact-form span::before {
  right: 3.5rem;
}

.button--contact-form span {
  letter-spacing: -0.03em;
  text-indent: -0.03em;
}

.button--white {
  color: #408F95;
  background-color: #fff;
  border: solid 1px #fff;
}

.button--white::before {
  border: solid 1px #fff;
}

.button--white::after {
  background-color: #408F95;
}

.button--white span::after {
  background-image: url("../images/common/more-accent.svg");
}

.button--white span::before {
  background-image: url("../images/common/more.svg");
}

@media (hover: hover) {
  .button--white:hover {
    color: #fff;
  }
}
