@use "global" as *;

// ----------------------------------------
// sp-nav.scss
// ----------------------------------------
.sp-nav__inner{
  height: 100vh;
  overflow: scroll;
  // 余白調整
  padding-bottom: rem(114);
}

.sp-nav__container{
  display: flex;
  justify-content: center;
  margin-left: rem(-20);
  // height: 100vh;
  // overflow: scroll;
}

.sp-nav__items + .sp-nav__items{
  margin-left: rem(7);
}

.sp-nav__item a{
  display: block;
  width: 100%;
  padding-top: rem(9);
  padding-bottom: rem(9);
  padding-left: rem(22);
  // text-align: center;
  font-size: rem(16);
  color: $white;
  font-weight: $bold;
  position: relative;
}

.sp-nav__title:before{
  content: '';
  display: inline-block;
  position: absolute;
  top: rem(12);
  left: 0;
  background-image: url('../images/common/hitode.png');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: rem(18);
  height: rem(18);
}
