@use "global" as *;

.page-campaign {
    position: relative;
}

.page-campaign::before {
    position: absolute;
    content: '';
    display: inline-block;
    width: rem(120);
    height: rem(55);
    aspect-ratio: 120 / 55;
    background-image: url('../images/common/page-campaign-fish.png');
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    top: rem(-24);
    left: calc(50% - max(45.5%, rem(172)));

    @include mq('md') {
        width: rem(301);
        height: rem(138);
        aspect-ratio: 301 / 138;
        top: rem(-71);
        left: calc(50% - min(48%, rem(620)));
        z-index: 10;
    }
}

.page-campaign__pagination {
    margin-top: rem(75);
}